.nav-bar{
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 5vw;

}
.logo{
    padding: 0 15px;
}
.nav-links{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.nav-links li{
    list-style: none;
    font-size: 18px;
    font-weight: 500;
    margin: 0 20px;
} 
.nav-links a{
    color: #333;
    text-decoration: none;
}
a{
    cursor: pointer;
}